import { Component, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-implementation-view-core",
  templateUrl: "./implementation-view-core.component.html",
  styleUrls: ["./implementation-view-core.component.scss"],
})
export class ImplementationViewCoreComponent
  extends AppHelpers
  implements OnInit
{
  implementationId: number;

  constructor() {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((param) => {
      this.implementationId = Number(param.id) ?? 0;
    });
  }
}
