import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IImplementationLectureModel,
  IImplementationLectureRequest,
} from "app/training/models/implementation/lectures";

@Injectable({
  providedIn: "root",
})
export class ImplementationLecturesService extends AppClientService {
  constructor() {
    super();
  }

  // getList(implementationId: number, params: IPageParams) {
  //   return this.get<IResponse<IImplementationLectureModel[]>>(
  //     "Training/ImplementationLecture/GetAll",
  //     {
  //       params: {
  //         ...params,
  //         $filter: `Implementation_ID eq ${implementationId}`,
  //       },
  //     }
  //   );
  // }

  getAll(implementationId: number) {
    return this.get<IResponse<IImplementationLectureModel[]>>(
      "Training/ImplementationLecture/GetAll",
      {
        params: {
          $filter: `Implementation_ID eq ${implementationId}`,
        },
      }
    );
  }

  create(data: IImplementationLectureRequest) {
    return this.post<IResponse<IImplementationLectureModel>>(
      "Training/ImplementationLecture/Create",
      data
    );
  }

  update(data: IImplementationLectureRequest) {
    return this.post<IResponse<IImplementationLectureModel>>(
      "Training/ImplementationLecture/Edit",
      data
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Training/ImplementationLecture/Delete", {
      params: { lecture_id: id },
    });
  }
}
