import { Component, OnInit, ViewChild } from "@angular/core";
import { MatStepper } from "@angular/material/stepper";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ImplementationEmployeesComponent } from "../implementation-employees/implementation-employees.component";
import { PRINT_SCREEN } from "@angular/cdk/keycodes";

@Component({
  selector: "app-add-implementation-core",
  templateUrl: "./add-implementation-core.component.html",
  styleUrls: ["./add-implementation-core.component.scss"],
})
export class AddImplementationCoreComponent extends AppHelpers implements OnInit {
  implementationId: number = 0;
  @ViewChild("stepper") stepper: MatStepper;
  @ViewChild(ImplementationEmployeesComponent)
  implementationEmployeesComponent: ImplementationEmployeesComponent;
  isGeneraCompleted: boolean = false;
  isEmployeesCompleted: boolean = false;
  isLecturesCompleted: boolean = false;
  isExpensesCompleted: boolean = false;

  constructor() {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((param) => {
      this.implementationId = Number(param.id ?? 0);
    });
  }

  pr() {
    console.log("isGeneraCompleted", this.isGeneraCompleted);
  }

  stepChanged() {
    // prevent inputs to be invalid after next & back
    this.stepper.selected.interacted = false;
  }

  getImplementationId(value) {
    this.implementationId = value;
  }

  updateEmployeeIndex() {
    this.implementationEmployeesComponent.getAll();
  }

  protected readonly PRINT_SCREEN = PRINT_SCREEN;
}
