import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IImplementationListModel,
  IImplementationModel,
  IImplementationRequest,
} from "app/training/models/implementation/implementation";
import { db } from "app/shared/resources/database-tabels";
import { map } from "rxjs/operators";
import { implementationToDisplay } from "app/training/mapper/implementation";

@Injectable({
  providedIn: "root",
})
export class ImplementationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IImplementationRequest) {
    return this.post<IResponse<IImplementationModel>>(
      "training/Implementation/Create",
      data
    );
  }

  update(data: IImplementationRequest) {
    return this.post<IResponse<IImplementationModel>>(
      "training/Implementation/Edit",
      data
    );
  }

  getAll() {
    return this.get<IResponse<IImplementationModel[]>>(
      "training/Implementation/GetAll",
      {
        params: {
          $expand: `${db.TRA_plan_course}.${db.trA_PLan},${db.TRA_plan_course}.${db.TRA_Course}`,
        },
      }
    );
  }

  getById(id: number) {
    return this.get<IResponse<IImplementationModel[]>>(
      "Training/Implementation/GetAll",
      {
        params: {
          $filter: `ID eq ${id}`,
          $expand: `${db.TRA_plan_course}.${db.trA_PLan},${db.Tra_Instructor},${db.TRA_TSP}`,
        },
      }
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IImplementationListModel[]>>(
      "training/Implementation/GetList",
      {
        params: params,
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("training/Implementation/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    return this.getAll().pipe(
      map((res) => {
        res.data = implementationToDisplay(res.data);
        return res;
      })
    );
  }
}
