import { Component, OnInit, ViewChild } from "@angular/core";
import { MatStepper } from "@angular/material/stepper";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { courseToDisplay } from "app/training/mapper/course";
import { ICourseModel, ICourseToDisplay, } from "app/training/models/course/course";
import { CourseGeneralService } from "app/training/services/course/course-general.service";
import { CourseService } from "app/training/services/course/course.service";

@Component({
  selector: "app-add-course",
  templateUrl: "./add-course.component.html",
  styleUrls: ["./add-course.component.scss"],
})
export class AddCourseComponent extends AppHelpers implements OnInit {
  // items: MenuItem[];
  // activeIndex: number = 0;
  course: ICourseToDisplay;
  courseId: number = 0;
  isloaded: boolean = false;
  @ViewChild("stepper") stepper: MatStepper;

  isGeneraCompleted: boolean = false;
  isTspInstructor: boolean = false;
  isCompetence: boolean = false;
  isOutline: boolean = false;

  constructor(
    private courseService: CourseService,
    private courseGeneralService: CourseGeneralService,
  ) {
    super();
    this.courseGeneralService.course$.next(null);
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("id")) ?? 0;
    });

    // this.loadStepItems();

    // this.translateService.onLangChange.subscribe((lang) =>
    //   this.loadStepItems()
    // );

    if (this.courseId > 0 && this.checkNoBusinessOrCompany()) {
      this.getCourseById();
    }
    else this.isloaded = true;
  }

  // ngAfterViewInit() {
  //   this.stepper._getIndicatorType = () => "number";
  // }

  // loadStepItems() {
  //   this.items = [
  //     {
  //       label: this.translateService.instant("general"),
  //     },
  //     {
  //       label: this.translateService.instant("tspAndInstructor"),
  //     },
  //     {
  //       label: this.translateService.instant("courseCompetence"),
  //     },
  //     {
  //       label: this.translateService.instant("outlineAndAttachments"),
  //     },
  //   ];
  // }

  getCourseById() {
    this.showLoading("core");
    this.courseService
      .getById(this.courseId)
      .subscribe((res: IResponseList<ICourseModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.course = courseToDisplay(res.data)[0];
          this.courseGeneralService.course$.next(this.course);
          this.isloaded = true;
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("core");
      });
  }

  getCourseId(value: number) {
    this.courseId = value;
  }

  OnDestroy() {
    this.courseGeneralService.course$.next(null);
  }

  // onCloseDialog() {
  //   this.dialogRef.close();
  // }

  stepChanged() {
    // prevent inputs to be invalid after next & back
    this.stepper.selected.interacted = false;
  }
}
