import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { courseCompetenceToDisplay } from "app/training/mapper/course-competence";
import {
  ICourseCompetenceDeleteParams,
  ICourseCompetenceModel,
  ICourseCompetenceRequest,
} from "app/training/models/course/competence";
import { CourseCompetenceService } from "app/training/services/course/course-competence.service";

@Component({
  selector: "app-course-competence",
  templateUrl: "./course-competence.component.html",
  styleUrls: ["./course-competence.component.scss"],
})
export class CourseCompetenceComponent extends AppHelpers implements OnInit {
  @Input() courseId: number;
  @Output() completed = new EventEmitter<boolean>(); // complete step to change icon
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    // "id",
    "competence",
    "competenceLevel",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;

  dataSource: MatTableDataSource<ICourseCompetenceModel>;

  constructor(private courseCompetenceService: CourseCompetenceService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      competenceId: [null, [Validators.required]],
      competenceLevelId: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.courseId > 0) this.getAll();
    else this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.courseCompetenceService
      .getAllByCourseId(this.courseId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICourseCompetenceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            courseCompetenceToDisplay(res.data)
          );
          this.isLoaded = true;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICourseCompetenceRequest = {
        course_ID: this.courseId,
        competence_ID: this.form.value.competenceId,
        competence_Level_ID: this.form.value.competenceLevelId,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: ICourseCompetenceRequest) {
    this.showLoading("competence");
    this.courseCompetenceService
      .create(model)
      .subscribe((res: IResponse<ICourseCompetenceModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("competence");
      });
  }

  onDelete(competence: ICourseCompetenceModel): void {
    let msg = competence.competence + " - " + competence.competenceLevel;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(competence);
    });
  }

  delete(competence: ICourseCompetenceModel) {
    let params: ICourseCompetenceDeleteParams = {
      CourseId: this.courseId,
      CompetenceDetailId: competence.competence_Detail_ID,
    };
    this.showLoading("competence");
    this.courseCompetenceService.Delete(params).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        this.getAll();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading("competence");
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }
}
