import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { courseToDisplay } from "app/training/mapper/course";
import {
  ICourseInstructorRequstModel,
  ICourseModel,
  ICourseToDisplay,
  ICourseTspRequstModel,
  trA_Instructor_Course,
  trA_TSP_Course,
} from "app/training/models/course/course";
import { CourseGeneralService } from "app/training/services/course/course-general.service";
import { CourseService } from "app/training/services/course/course.service";

@Component({
  selector: "app-course-tsp-and-instructor",
  templateUrl: "./course-tsp-and-instructor.component.html",
  styleUrls: ["./course-tsp-and-instructor.component.scss"],
})
export class CourseTspAndInstructorComponent extends AppHelpers implements OnInit {
  @Input() courseId: number;
  @Output() completed = new EventEmitter<boolean>(); // complete step to change icon

  isLoaded: boolean = true;
  course: ICourseToDisplay;

  tspform: FormGroup;
  intstrucorForm: FormGroup;
  submitted: boolean = false;
  mandatoryTspColomns: string[] = [
    // "Ser",
    "Name",
    "location",
  ];
  optionalTspColomns: string[] = [];
  mandatoryIsnstructorColomns: string[] = [
    // "Ser",
    "Name",
    "jobTitle",
  ];
  optionalIsnstructorColomns: string[] = [];

  tspDataSource: MatTableDataSource<trA_TSP_Course>;
  instructorDataSource: MatTableDataSource<trA_Instructor_Course>;
  instructorForm: FormGroup;

  constructor(
    private courseService: CourseService,
    private courseGeneralService: CourseGeneralService
  ) {
    super();
    this.course = this.courseGeneralService.course$.getValue();
    // this.courseGeneralService.course$.subscribe((course) => {
    //   this.course = course;
    //   if (this.course != null) this.getFormData();
    // });
  }

  ngOnInit(): void {
    this.tspform = this.fb.group({
      tsP_IDs: [[], [Validators.required]],
    });

    this.intstrucorForm = this.fb.group({
      instructor_IDs: [[], [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.tspDataSource?.data?.length > 0)
      this.tspDataSource = new MatTableDataSource(null);
    if (this.instructorDataSource?.data?.length > 0)
      this.instructorDataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.courseId > 0)
      this.getCourseById();
    else this.isLoaded = true;
  }

  // getFormData() {
  //   this.tspform.controls.tsP_IDs.setValue(
  //     this.course.trA_TSP_Course?.map((r) => {
  //       return r.tsP_ID;
  //     })
  //   );
  //   this.tspDataSource = new MatTableDataSource(this.course.trA_TSP_Course);

  //   this.intstrucorForm.controls.instructor_IDs.setValue(
  //     this.course.trA_Instructor_Course?.map((r) => {
  //       return r.instructor_ID;
  //     })
  //   );
  //   this.instructorDataSource = new MatTableDataSource(
  //     this.course.trA_Instructor_Course
  //   );
  // }

  submitTsp() {
    this.submitted = true;
    if (this.tspform.valid) {
      let data = {} as ICourseTspRequstModel;
      data.Course_ID = this.courseId;
      data.TSP_IDs = this.tspform.controls.tsP_IDs.value;

      this.showLoading("tsp");
      this.courseService.addTSPToCourse(data).subscribe((res) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res.message.messageTypeName);
          this.getCourseById();

          this.submitted = false;
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("tsp");
      });
    }
  }

  submitInstructor() {
    this.submitted = true;
    if (this.intstrucorForm.valid) {
      let data = {} as ICourseInstructorRequstModel;
      data.Course_ID = this.courseId;
      data.Instructor_IDs = this.intstrucorForm.controls.instructor_IDs.value;

      this.showLoading("tsp");
      this.courseService.addInstructorToCourse(data).subscribe((res) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res.message.messageTypeName);
          this.getCourseById();
          this.completed.emit(true);
          this.submitted = false;
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("tsp");
      });
    }
  }

  getCourseById() {
    this.tspDataSource = new MatTableDataSource(null);
    this.instructorDataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    // this.showLoading("tsp");
    this.courseService
      .getById(this.courseId)
      .subscribe((res: IResponseList<ICourseModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.course = courseToDisplay(res.data)[0];
          // this.courseGeneralService.course$.next(courseToDisplay(res.data)[0]);

          this.instructorDataSource = new MatTableDataSource(
            this.course.trA_Instructor_Course
          );
          this.tspDataSource = new MatTableDataSource(
            this.course.trA_TSP_Course
          );
        }
        else this.errorList(res?.message?.messages);
        // this.hideLoading("tsp");
      });
  }
}
