import { Component, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-plan-view-core",
  templateUrl: "./plan-view-core.component.html",
  styleUrls: ["./plan-view-core.component.scss"],
})
export class PlanViewCoreComponent extends AppHelpers implements OnInit {
  planId: number;

  showTna: boolean = false;
  showCourses: boolean = false;

  updateCourseDropdown: boolean = false;

  viewsManager = ViewsManager;

  constructor() {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((param) => {
      this.planId = Number(param.id) ?? 0;
    });
  }

  scrollToSection(sectionId: string) {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: "smooth", block: "start" });
    }
  }
}
