import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { implementationExpenseToDisplay } from "app/training/mapper/implementation";
import {
  IImplementationExpensesDeleteParams,
  IImplementationExpensesDisplayModel,
  IImplementationExpensesModel,
  IImplementationExpensesRequest,
} from "app/training/models/implementation/expenses";
import { ImplementationExpensesService } from "app/training/services/implementation/implementation-expenses.service";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-implementation-expenses",
  templateUrl: "./implementation-expenses.component.html",
  styleUrls: ["./implementation-expenses.component.scss"],
})
export class ImplementationExpensesComponent
  extends AppHelpers
  implements OnInit
{
  @Input() implementationId: number = 0;
  @Input() isView: boolean = false;
  @Output() completed = new EventEmitter<boolean>(); // complete step to change icon

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "expense",
    "value",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<IImplementationExpensesDisplayModel>;
  // use in view to detect update in view (not update after add)
  isViewUpdate: boolean = false;

  constructor(
    private implementationExpensesService: ImplementationExpensesService
  ) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      // id: [0, { nonNallable: true }],
      expenseId: [null, [Validators.required]],
      budget: [null, [Validators.required, CustomValidators.gte(0)]],
    });

    if (this.isView) this.changeIsViewUpdate(false);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.implementationId?.currentValue > 0 &&
      this.checkNoBusinessOrCompany()
    )
      this.getAll();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);

    if (this.checkNoBusinessOrCompany() && this.implementationId > 0)
      this.getAll();
    else this.isLoaded = true;
  }

  override onModulesChanged(): void {
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.implementation_expense
    );
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.implementationExpensesService
      .getAll(this.implementationId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IImplementationExpensesModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            implementationExpenseToDisplay(res.data)
          );
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IImplementationExpensesRequest = {
        implementation_ID: this.implementationId,
        expense_ID: this.form.value.expenseId,
        expense_Value: this.form.value.budget,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IImplementationExpensesRequest) {
    this.showLoading("expenses");
    this.implementationExpensesService
      .create(model)
      .subscribe((res: IResponse<IImplementationExpensesModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.clearForm();
          this.getAll();
          this.completed.emit(true);
        } else this.errorList(res?.message?.messages);
        this.hideLoading("expenses");
      });
  }

  onDelete(expense: IImplementationExpensesDisplayModel): void {
    let msg = expense.expense + " - " + expense.value;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(expense);
    });
  }

  delete(expense: IImplementationExpensesDisplayModel) {
    let deleteParams: IImplementationExpensesDeleteParams = {
      implementation_id: expense.implementation_ID,
      expense_id: expense.expense_ID,
    };
    this.showLoading("expenses");
    this.implementationExpensesService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading("expenses");
      });
  }

  clearForm() {
    this.form.reset(/* { id: 0 } */);
    this.submitted = false;
  }

  changeIsViewUpdate(value: boolean) {
    this.isViewUpdate = value;
    let actionsIndex = this.mandatoryColomns.findIndex(
      (col) => col == "actions"
    );
    let cols = this.mandatoryColomns.slice();
    if (value && actionsIndex < 0) cols.push("actions");
    else if (!value && actionsIndex >= 0) cols.pop();
    this.mandatoryColomns = cols;
  }
}
