import { Component, EventEmitter, OnInit, Output } from "@angular/core";

@Component({
  selector: "course-general-data",
  templateUrl: "./course-general-data.component.html",
  styleUrls: ["./course-general-data.component.scss"],
})
export class CourseGeneralDataComponent implements OnInit {
  @Output() courseId = new EventEmitter<number>();

  constructor() {}

  ngOnInit(): void {}

  getCourseId(value: number) {
    this.courseId.emit(value);
  }
}
