import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import {
  IImplementationDisplayModel,
  IImplementationModel,
  IImplementationRequest,
} from "app/training/models/implementation/implementation";
import { ImplementationService } from "app/training/services/implementation/implementation.service";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-implementation-general-data",
  templateUrl: "./implementation-general-data.component.html",
  styleUrls: ["./implementation-general-data.component.scss"],
})
export class ImplementationGeneralDataComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  @Input() implementationId: number = 0;
  @Output() changeImplementationId = new EventEmitter<number>();
  @Output() updateEmployeeIndex = new EventEmitter<number>(); // after update
  @Output() completed = new EventEmitter<boolean>(); // complete step to change icon

  // use in view
  @Input() implementation: IImplementationDisplayModel;
  @Output() closeAfterUpdate = new EventEmitter<boolean>();

  constructor(private implementationService: ImplementationService) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.implementationId > 0 && this.checkNoBusinessOrCompany())
      this.getImplementationById();
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [this.implementationId, {nonNullable: true}],
        plan_ID: [null, [Validators.required]],
        plan_Course_ID: [null, [Validators.required]],
        instructor_ID: [null, [Validators.required]],
        tsP_ID: [null, [Validators.required]],
        from_Date: [null, [Validators.required]],
        to_Date: [null, [Validators.required]],
        attendance_Percentage: [
          null,
          [
            Validators.required,
            CustomValidators.gte(0),
            CustomValidators.lte(100),
          ],
        ],
        note: [null],
      },
      {
        validator: CustomValidator.compareDates("from_Date", "to_Date"),
      }
    );

    if (this.implementation) this.implementationId = this.implementation.id;
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      let model: IImplementationRequest = {
        id: this.formCtrls.id.value,
        plan_Course_ID: this.formCtrls.plan_Course_ID.value,
        instructor_ID: this.formCtrls.instructor_ID.value,
        tsP_ID: this.formCtrls.tsP_ID.value,
        from_Date: HelpersFunctions.mapDateReverse(
          this.formCtrls.from_Date.value
        ),
        to_Date: HelpersFunctions.mapDateReverse(this.formCtrls.to_Date.value),
        attendance_Percentage: this.formCtrls.attendance_Percentage.value,
        note: this.formCtrls.note.value,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    }
  }

  getImplementationById() {
    // edit in view
    if (this.implementation) {
      this.formCtrls.plan_ID.setValue(
        this.implementation.trA_Plan_Course.plan_ID
      );
      this.form.patchValue(this.implementation);
    }
    // edit after add if change company
    else {
      this.showLoading("general");
      this.implementationService
        .getById(this.implementationId)
        .subscribe((res: IResponse<IImplementationModel[]>) => {
          if ((res.message.messageType = MessageTypes.Success)) {
            this.form.patchValue(res.data[0]);
            this.formCtrls.plan_ID.setValue(
              res.data[0].trA_Plan_Course?.plan_ID
            );
            this.formCtrls.plan_Course_ID.setValue(res.data[0].plan_Course_ID);
          }
          else this.errorList(res?.message?.messages);
          this.hideLoading("general");
        });
    }
  }

  create(data: IImplementationRequest) {
    this.showLoading("general");
    this.implementationService
      .create(data)
      .subscribe((res: IResponse<IImplementationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.implementationId = res.data.id;
          this.formCtrls.id.setValue(this.implementationId);
          let url = `/training/${this.moduleId}/implementation/edit/${this.implementationId}`;
          this.navigateTo(url);
          this.changeImplementationId.emit(this.implementationId);
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("general");
      });
  }

  update(data: IImplementationRequest) {
    this.showLoading("general");
    this.implementationService
      .update(data)
      .subscribe((res: IResponse<IImplementationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.closeAfterUpdate.emit(true);
          this.updateEmployeeIndex.emit();
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("general");
      });
  }

  clearForm() {
    this.form.reset();
    this.submitted = false;
  }
}
