import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { implementationEmployeeToDisplay } from "app/training/mapper/implementation";
import {
  IImplementationEmployeeDeleteParams,
  IImplementationEmployeeDisplayModel,
  IImplementationEmployeeModel,
  IImplementationEmployeeRequest,
} from "app/training/models/implementation/employees";
import { ImplementationEmployeeService } from "app/training/services/implementation/implementation-employee.service";

@Component({
  selector: "app-implementation-employees",
  templateUrl: "./implementation-employees.component.html",
  styleUrls: ["./implementation-employees.component.scss"],
})
export class ImplementationEmployeesComponent
  extends AppHelpers
  implements OnInit
{
  @Input() implementationId: number = 0;
  @Input() isView: boolean = false;
  @Output() completed = new EventEmitter<boolean>(); // complete step to change icon
  mandatoryColomns: string[] = [
    // "Ser",
    "code",
    "employee",
    "department",
    "job",
    "selectAll",
  ];
  mandatoryColomnsView: string[] = [
    // "Ser",
    "code",
    "employee",
    "department",
    "job",
    "actions",
  ];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<IImplementationEmployeeDisplayModel>;
  dataSourceSelected: MatTableDataSource<IImplementationEmployeeDisplayModel>;
  optionalColomns: string[] = [];
  // use in view to detect update in view (not update after add)
  isViewUpdate: boolean = true;

  constructor(
    private implementationEmployeeService: ImplementationEmployeeService
  ) {
    super();
  }

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges): void {
    // after add implementation general data
    if (
      changes.implementationId.currentValue > 0 &&
      !changes.implementationId.previousValue &&
      !changes.implementationId.firstChange && // first time will be 0
      this.checkNoBusinessOrCompany()
    ) {
      this.getAll();
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) {
      this.dataSource = new MatTableDataSource(null);
    }

    if (this.checkNoBusinessOrCompany() && this.implementationId > 0) {
      this.getAll();
    } else {
      this.isLoaded = true;
    }
  }

  override onModulesChanged(): void {
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.implementation_employees
    );
  }

  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.dataSourceSelected = new MatTableDataSource(null);
    this.isLoaded = false;

    this.implementationEmployeeService
      .getAll(this.implementationId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IImplementationEmployeeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // display selected first
          res.data.sort((a, b) =>
            a.isSelected === b.isSelected ? 0 : a.isSelected ? -1 : 1
          );
          let data = implementationEmployeeToDisplay(res.data);
          this.dataSource = new MatTableDataSource(data);
          this.dataSourceSelected = new MatTableDataSource(
            data.filter((ele) => ele.isSelected)
          );
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    const model: IImplementationEmployeeRequest = {
      Implementation_ID: this.implementationId,
      SelectedEmployees: [],
    };

    this.dataSource.data
      .filter((ele) => ele.isSelected)
      .forEach((selected) => {
        model.SelectedEmployees.push(
          selected.person.core_Person_Instance[0].id
        );
      });

    if (model.SelectedEmployees.length == 0) {
      this.warning("selectOneEmployee");
      return;
    }

    this.create(model);
  }

  create(model: IImplementationEmployeeRequest) {
    this.showLoading();
    this.implementationEmployeeService
      .create(model)
      .subscribe((res: IResponse<IImplementationEmployeeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (this.isView) this.isViewUpdate = false;
          this.completed.emit(true);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onDelete(employee: IImplementationEmployeeDisplayModel): void {
    let msg = employee.employee;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(employee);
      }
    });
  }

  delete(employee: IImplementationEmployeeDisplayModel) {
    let params: IImplementationEmployeeDeleteParams = {
      implementation_id: this.implementationId,
      person_instance_id: employee.person.id,
    };
    this.showLoading();
    this.implementationEmployeeService
      .Delete(params)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onClearAll(): void {
    let msg = this.translate("allEmployees");
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.clearAll();
      }
    });
  }

  clearAll() {
    this.showLoading();
    this.implementationEmployeeService
      .DeleteAll(this.implementationId)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.dataSourceSelected = new MatTableDataSource([]);
          this.dataSource.data.map((ele) => (ele.isSelected = false));
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }
}
