import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { implementationListToDisplay } from "app/training/mapper/implementation";
import { IImplementationListDisplayModel, IImplementationListModel } from "app/training/models/implementation/implementation";
import { ImplementationService } from "app/training/services/implementation/implementation.service";

@Component({
  selector: "app-implementation-index",
  templateUrl: "./implementation-index.component.html",
  styleUrls: ["./implementation-index.component.scss"],
})
export class ImplementationIndexComponent extends AppHelpers implements OnInit {
  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "planName",
    "course",
    "from_Date",
    "to_Date",
    "count_Employees",
    "actions",
  ];
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IImplementationListDisplayModel>;
  isLoaded: boolean = false;

  constructor(public implementationService: ImplementationService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.removePageParams();
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.implementationService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IImplementationListModel[]>) => {
        if (res.message.messageType == 1) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(
            implementationListToDisplay(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  onDelete(implementation: IImplementationListDisplayModel): void {
    let msg = implementation.planName + " - " + implementation.course;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(implementation);
    });
  }

  delete(implementation: IImplementationListDisplayModel) {
    this.showLoading();
    this.implementationService
      .Delete(implementation.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == 1) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.pageSize);
          }
          else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.pageSize);
          }
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }
}
