import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IImplementationExpensesDeleteParams,
  IImplementationExpensesModel,
  IImplementationExpensesRequest,
} from "app/training/models/implementation/expenses";

@Injectable({
  providedIn: "root",
})
export class ImplementationExpensesService extends AppClientService {
  constructor() {
    super();
  }

  getAll(implementationId: number) {
    return this.get<IResponse<IImplementationExpensesModel[]>>(
      "Training/ImplementationExpenses/GetAll",
      {
        params: {
          $filter: `Implementation_ID eq ${implementationId}`,
          $expand: `${db.TRA_expense}`,
        },
      }
    );
  }

  create(data: IImplementationExpensesRequest) {
    return this.post<IResponse<IImplementationExpensesModel>>(
      "Training/ImplementationExpenses/Create",
      data
    );
  }

  Delete(params: IImplementationExpensesDeleteParams) {
    return this.delete<IResponse>("Training/ImplementationExpenses/Delete", {
      params: params,
    });
  }
}
