import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IImplementationEmployeeDeleteParams,
  IImplementationEmployeeModel,
  IImplementationEmployeeRequest,
} from "app/training/models/implementation/employees";

@Injectable({
  providedIn: "root",
})
export class ImplementationEmployeeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IImplementationEmployeeRequest) {
    return this.post<IResponse<IImplementationEmployeeModel>>(
      "training/ImplementationEmployees/Create",
      data
    );
  }

  getAll(implementationId: number) {
    return this.get<IResponse<IImplementationEmployeeModel[]>>(
      "Training/ImplementationEmployees/GetEmployees",
      {
        params: {
          Implementation_ID: implementationId,
        },
      }
    );
  }

  Delete(params: IImplementationEmployeeDeleteParams) {
    return this.delete<IResponse>("Training/ImplementationEmployees/Delete", {
      params: params,
    });
  }

  DeleteAll(implementationId: number) {
    return this.delete<IResponse>(
      "Training/ImplementationEmployees/DeleteAll",
      {
        params: { implementation_id: implementationId },
      }
    );
  }
}
