import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { implementationLectureToDisplay } from "app/training/mapper/implementation";
import {
  IImplementationLectureModel,
  IImplementationLectureRequest,
} from "app/training/models/implementation/lectures";
import { ImplementationLecturesService } from "app/training/services/implementation/implementation-lectures.service";

@Component({
  selector: "app-implementation-lectures",
  templateUrl: "./implementation-lectures.component.html",
  styleUrls: ["./implementation-lectures.component.scss"],
})
export class ImplementationLecturesComponent
  extends AppHelpers
  implements OnInit
{
  @Input() implementationId: number = 0;
  @Input() isView: boolean = false;
  @Output() completed = new EventEmitter<boolean>(); // complete step to change icon
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "ar_Name",
    "en_Name",
    "dayDate",
    "startHour",
    "endHour",
    "location",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<IImplementationLectureModel>;
  // use in view to detect update in view (not update after add)
  isViewUpdate: boolean = false;

  constructor(
    private implementationLectureService: ImplementationLecturesService
  ) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group(
      {
        id: [0, { nonNallable: true }],
        ar_Name: [null, [Validators.required]],
        en_Name: [null, [Validators.required]],
        day_Date: [null, [Validators.required]],
        start_Houre: [null, [Validators.required]],
        end_Houre: [null, [Validators.required]],
        location: [null],
        note: [null],
        isRepeated: [null],
        repeatEndDate: [null],
        days: [[]],
      },
      {
        validator: [
          CustomValidator.timeBefore("start_Houre", "end_Houre"),
          CustomValidator.compareDates("day_Date", "repeatEndDate"),
        ],
      }
    );

    this.formCtrls.isRepeated.valueChanges.subscribe((isRepeated) =>
      this.onChangeIsRepeated(isRepeated)
    );

    if (this.isView) this.changeIsViewUpdate(false);
  }

  // ngOnChanges(changes: SimpleChanges): void {
  //   if (
  //     changes.implementationId?.currentValue > 0 &&
  //     this.checkNoBusinessOrCompany()
  //   )
  //     this.getAll(this.firstPage);
  // }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);

    if (this.checkNoBusinessOrCompany() && this.implementationId > 0)
      this.getAll();
    else this.isLoaded = true;
  }

  override onModulesChanged(): void {
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.implementation_lecture
    );
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.implementationLectureService
      .getAll(this.implementationId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IImplementationLectureModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            implementationLectureToDisplay(res.data)
          );
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IImplementationLectureRequest = {
        implementation_ID: this.implementationId,
        id: this.form.value.id,
        ar_Name: this.form.value.ar_Name,
        en_Name: this.form.value.en_Name,
        day_Date: HelpersFunctions.mapDateReverse(this.form.value.day_Date),
        start_Houre: this.form.value.start_Houre,
        end_Houre: this.form.value.end_Houre,
        location: this.form.value.location,
        note: this.form.value.note,
        isRepeated: this.form.value.isRepeated ?? false,
        repeatEndDate: null,
        weekDays: [],
      };
      if (model.isRepeated) {
        model.repeatEndDate = HelpersFunctions.mapDateReverse(
          this.form.value.repeatEndDate
        );
        model.weekDays = this.form.value.days ?? [];
      }

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IImplementationLectureRequest) {
    this.showLoading("lecture");
    this.implementationLectureService
      .create(model)
      .subscribe((res: IResponse<IImplementationLectureModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.clearForm();
          this.getAll();
          // if (this.isView) this.changeIsViewUpdate(false);
          this.completed.emit(true);
        } else this.errorList(res?.message?.messages);
        this.hideLoading("lecture");
      });
  }

  update(model: IImplementationLectureRequest) {
    this.showLoading("lecture");
    this.implementationLectureService
      .update(model)
      .subscribe((res: IResponse<IImplementationLectureModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.clearForm();
          this.getAll();
          // if (this.isView) this.changeIsViewUpdate(false);
          this.completed.emit(true);
        } else this.errorList(res?.message?.messages);
        this.hideLoading("lecture");
      });
  }

  lectureToEdit(lecture: IImplementationLectureModel) {
    this.form.patchValue(lecture);
    // console.log(this.form);
  }

  onDelete(lecture: IImplementationLectureModel): void {
    let msg = this.msgTranslate(lecture.ar_Name, lecture.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(lecture);
    });
  }

  delete(lecture: IImplementationLectureModel) {
    this.showLoading("lecture");
    this.implementationLectureService
      .Delete(lecture.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading("lecture");
      });
  }

  clearForm() {
    this.form.reset({ id: 0, days: [], isRepeated: false });
    this.submitted = false;
  }

  onChangeIsRepeated(value) {
    // console.log(value);
    if (value) {
      this.formCtrls.repeatEndDate.addValidators([Validators.required]);
      this.formCtrls.days.addValidators([Validators.required]);
    } else {
      this.formCtrls.repeatEndDate.clearValidators();
      this.formCtrls.days.clearValidators();
    }
    this.formCtrls.repeatEndDate.updateValueAndValidity();
    this.formCtrls.days.updateValueAndValidity();
  }

  changeIsViewUpdate(value: boolean) {
    this.isViewUpdate = value;
    let actionsIndex = this.mandatoryColomns.findIndex(
      (col) => col == "actions"
    );
    let cols = this.mandatoryColomns.slice();
    if (value && actionsIndex < 0) cols.push("actions");
    else if (!value && actionsIndex >= 0) cols.pop();
    this.mandatoryColomns = cols;
  }
}
