import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ITrainingPlanListModel } from "app/training/models/training-plan/training-plan";
import { TrainingPlanIndexService } from "app/training/services/traning-plan/training-plan-index.service";

@Component({
  selector: "app-training-plan-index",
  templateUrl: "./training-plan-index.component.html",
  styleUrls: ["./training-plan-index.component.scss"],
})
export class TrainingPlanIndexComponent extends AppHelpers implements OnInit {
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ITrainingPlanListModel>;

  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "ar_Name",
    "en_Name",
    "from_Date",
    "to_Date",
    "budget",
    "count_Courses",
    "count_Persons",
    "actions",
  ];
  optionalColomns: string[] = ["note"];

  constructor(public TrainingPlanIndexService: TrainingPlanIndexService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  ngOnInit(): void {}

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    this.removePageParams();
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.TrainingPlanIndexService.getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ITrainingPlanListModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          res.data.map((e) => {
            (e.id = e.plan_ID),
              (e.from_Date = HelpersFunctions.mapDate(e.from_Date));
            e.to_Date = HelpersFunctions.mapDate(e.to_Date);
          });
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  delete(model: ITrainingPlanListModel) {
    this.showLoading();
    this.TrainingPlanIndexService.Delete(model.id).subscribe(
      (res: IResponse) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            this.getList(this.prevPage, this.page.pageSize);
          }
          else {
            this.getList(this.currentPage, this.page.pageSize);
          }
          this.deleteSuccessfullyMsg(res.message.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      }
    );
  }

  onDelete(model: ITrainingPlanListModel) {
    let msg = this.msgTranslate(model.ar_Name, model.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(model);
    });
  }
}
