import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CourseIndexComponent } from "app/training/components/courses/course-index/course-index.component";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { TspIndexComponent } from "./components/tsp/tsp-index/tsp-index.component";
import { AddTspComponent } from "./components/tsp/add-tsp/add-tsp.component";
import { InstructorIndexComponent } from "./components/instructor/instructor-index/instructor-index.component";
import { AddInstructorComponent } from "./components/instructor/add-instructor/add-instructor.component";
import { AddCourseComponent } from "app/training/components/courses/add-course/add-course-core/add-course.component";
import { TrainingRequestIndexComponent } from "./components/training-requests/training-request-index/training-request-index.component";
import { AddTrainingRequestComponent } from "./components/training-requests/add-training-request/add-training-request.component";
import { TrainingPlanIndexComponent } from "./components/training-plan/training-plan-index/training-plan-index.component";
import { PlanCoreComponent } from "./components/training-plan/add-paln/plan-core/plan-core.component";
import { PlanViewCoreComponent } from "./components/training-plan/plan-view/plan-view-core/plan-view-core.component";
import { ImplementationIndexComponent } from "./components/implementation/implementation-index/implementation-index.component";
import {
  AddImplementationCoreComponent
} from "./components/implementation/add-implementation/add-implementation-core/add-implementation-core.component";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";
import { ImplementationViewCoreComponent } from "./components/implementation/implementation-view/implementation-view-core/implementation-view-core.component";

const routes: Routes = [
  {
    path: ViewsManager.course, // course
    data: {view: ViewsManager.course},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: CourseIndexComponent,
        data: {view: ViewsManager.course_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddCourseComponent,
        data: {view: ViewsManager.add_course},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: AddCourseComponent,
        data: {view: ViewsManager.add_course},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "tsp", //tsp
    data: {view: ViewsManager.tsp},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: TspIndexComponent,
        data: {view: ViewsManager.tsp_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddTspComponent,
        data: {view: ViewsManager.add_tsp},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: AddTspComponent,
        data: {view: ViewsManager.add_tsp},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "instructor", //instructor
    data: {view: ViewsManager.instructor},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: InstructorIndexComponent,
        data: {view: ViewsManager.instructor_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddInstructorComponent,
        data: {view: ViewsManager.add_instructor},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: AddInstructorComponent,
        data: {view: ViewsManager.add_instructor},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "training-request", //training-request
    data: {view: ViewsManager.training_request},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: TrainingRequestIndexComponent,
        data: {view: ViewsManager.training_request_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddTrainingRequestComponent,
        data: {view: ViewsManager.add_training_request},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: AddTrainingRequestComponent,
        data: {view: ViewsManager.add_training_request},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "training-plan", //training-plan
    data: {view: ViewsManager.training_plan},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: TrainingPlanIndexComponent,
        data: {view: ViewsManager.training_plan_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: PlanCoreComponent,
        data: {view: ViewsManager.add_training_plan},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: PlanViewCoreComponent,
        data: {view: ViewsManager.add_training_plan},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },

  {
    path: "implementation", // implementation
    data: {view: ViewsManager.implementation},
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: ImplementationIndexComponent,
        data: {view: ViewsManager.implementation_index},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddImplementationCoreComponent,
        data: {view: ViewsManager.add_implementation},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id",
        component: ImplementationViewCoreComponent,
        data: {view: ViewsManager.add_implementation},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TrainingRoutingModule {}
