import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import {
  IImplementationEmployeeDisplayModel,
  IImplementationEmployeeModel,
} from "../models/implementation/employees";
import {
  IImplementationExpensesDisplayModel,
  IImplementationExpensesModel,
} from "../models/implementation/expenses";
import {
  IImplementationDisplayModel,
  IImplementationListDisplayModel,
  IImplementationListModel,
  IImplementationModel,
} from "../models/implementation/implementation";
import {
  IImplementationLectureDisplayModel,
  IImplementationLectureModel,
} from "../models/implementation/lectures";

export function implementationToDisplay(
  data: IImplementationModel[]
): IImplementationDisplayModel[] {
  return data.map((ele) => {
    return <IImplementationDisplayModel>{
      ...ele,
      plan: msgTranslate(
        ele.trA_Plan_Course?.trA_PLan?.ar_Name,
        ele.trA_Plan_Course?.trA_PLan?.en_Name
      ),
      course: ele.trA_Plan_Course?.name,
      fromDate: HelpersFunctions.mapDate(ele.from_Date),
      toDate: HelpersFunctions.mapDate(ele.to_Date),
      instructor: msgTranslate(
        ele.trA_Instructor?.ar_Name,
        ele.trA_Instructor?.en_Name
      ),
      tsp: msgTranslate(ele.trA_TSP?.ar_Name, ele.trA_TSP?.en_Name),
    };
  });
}

export function implementationListToDisplay(
  data: IImplementationListModel[]
): IImplementationListDisplayModel[] {
  return data.map((ele) => {
    return <IImplementationListDisplayModel>{
      ...ele,
      id: ele.impelementation_ID,
      planName: msgTranslate(ele.plan?.ar_Name, ele.plan?.en_Name),
      course: ele.plan_Course?.name,
      from_Date: HelpersFunctions.mapDate(ele.from_Date),
      to_Date: HelpersFunctions.mapDate(ele.to_Date),
    };
  });
}

export function implementationExpenseToDisplay(
  data: IImplementationExpensesModel[]
): IImplementationExpensesDisplayModel[] {
  return data.map((ele) => {
    return <IImplementationExpensesDisplayModel>{
      ...ele,
      expense: msgTranslate(ele.trA_Expense?.ar_Name, ele.trA_Expense?.en_Name),
      value: ele.expense_Value,
    };
  });
}

export function implementationEmployeeToDisplay(
  data: IImplementationEmployeeModel[]
): IImplementationEmployeeDisplayModel[] {
  return data.map((ele) => {
    return <IImplementationEmployeeDisplayModel>{
      ...ele,
      code: ele.person?.code,
      employee: msgTranslate(ele.person?.ar_Name, ele.person?.en_Name),
      department: msgTranslate(ele.org?.ar_Name, ele.org?.en_Name),
      job: msgTranslate(ele.job_Title?.ar_Name, ele.job_Title?.en_Name),
    };
  });
}

export function implementationLectureToDisplay(
  data: IImplementationLectureModel[]
): IImplementationLectureDisplayModel[] {
  return data.map((ele) => {
    return <IImplementationLectureDisplayModel>{
      ...ele,
      dayDate: HelpersFunctions.mapDate(ele.day_Date),
      startHour: HelpersFunctions.mapTime(ele.start_Houre),
      endHour: HelpersFunctions.mapTime(ele.end_Houre),
      notes: ele.note,
    };
  });
}
