import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { ITrainingReqModel, ITrainingReqRequest } from "app/training/models/training-request";
import { TrainingRequestService } from "app/training/services/training-request.service";
import { IReasonsModel } from "app/system-setup/training-setup/models/reasons";

@Component({
  selector: "app-add-training-request",
  templateUrl: "./add-training-request.component.html",
  styleUrls: ["./add-training-request.component.scss"],
})
export class AddTrainingRequestComponent extends AppHelpers implements OnInit {
  trainingRequestId: number = 0;
  form: FormGroup;
  submitted: boolean = false;
  allowBusinessObjective: boolean = false;

  constructor(private trainingRequestService: TrainingRequestService) {
    super();
    this.activatedRoute.params.subscribe((param) => {
      this.trainingRequestId = Number(param.id ?? 0);
    });
  }

  override onChangeBusinessAndCompanyId() {
    if (this.trainingRequestId > 0 && this.checkNoBusinessOrCompany()) {
      this.getTrainingRequestById();
    }
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [this.trainingRequestId, {nonNullable: true}],
        course_Category_ID: [null, Validators.required],
        course_ID: [null, [Validators.required]],
        reason_ID: [null, [Validators.required]],
        from_Date: [null, Validators.required],
        to_Date: [null, Validators.required],
        note: [null],
        employee_id: [null, Validators.required],
        SelectedBusinessObjectiveIDs: [],
      },
      {
        validator: CustomValidator.compareDates("from_Date", "to_Date"),
      }
    );

  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      let body: ITrainingReqRequest = {
        training_request: {
          id: this.formCtrls.id.value,
          course_ID: this.formCtrls.course_ID.value,
          reason_ID: this.formCtrls.reason_ID.value,
          from_Date: HelpersFunctions.mapDateReverse(this.formCtrls.from_Date.value),
          to_Date: HelpersFunctions.mapDateReverse(this.formCtrls.to_Date.value),
          note: this.formCtrls.note.value,
        },
        SelectedSubOrdenateEmployeeInstanceIDs: this.formCtrls.employee_id.value,
        SelectedBusinessObjectiveIDs: this.formCtrls.SelectedBusinessObjectiveIDs.value
      };
      // console.log(body)
      body.training_request.id > 0 ? this.update(body) : this.create(body);
    }
  }

  getTrainingRequestById() {
    this.showLoading();
    this.trainingRequestService
      .getById(this.trainingRequestId)
      .subscribe((res: IResponse<ITrainingReqModel>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.formCtrls.course_Category_ID.setValue(res.data.training_request.trA_Course.course_Category_ID);
          this.form.patchValue(res.data.training_request);
          this.formCtrls.SelectedBusinessObjectiveIDs.setValue(res.data.selectedBusinessObjectiveIDs);
          this.formCtrls.employee_id.setValue(res.data.selectedSubOrdenateEmployeeInstanceIDs);
          this.onReasonChange([res.data.training_request.trA_Reason]);
        }
        this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  create(data: ITrainingReqRequest) {
    this.showLoading();
    this.trainingRequestService
      .create(data)
      .subscribe((res: IResponse<ITrainingReqRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          // this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.trainingRequestId = res.data.training_request.id;
          this.formCtrls.id.setValue(this.trainingRequestId);
          let url = `/training/${this.moduleId}/training-request/edit/${this.trainingRequestId}`;
          this.navigateTo(url);
          // console.log(this.form.value)
          // console.log(res)
          // console.log(url)
        }
        else {
          this.errorList(res?.message?.messages);
        }
        this.hideLoading();
      });
  }

  update(data: ITrainingReqRequest) {
    this.showLoading();
    this.trainingRequestService
      .update(data)
      .subscribe((res: IResponse<ITrainingReqRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        }
        else {
          this.errorList(res?.message?.messages);
        }
        this.hideLoading();
      });
  }


  onReasonChange(model: IReasonsModel[]) {
    if (model[0]?.hasBusinessObjectives) {
      this.allowBusinessObjective = true;
      this.formCtrls?.SelectedBusinessObjectiveIDs.addValidators([Validators.required]);
    }
    else {
      this.allowBusinessObjective = false;
      this.formCtrls?.SelectedBusinessObjectiveIDs.setValue([]);
      this.formCtrls?.SelectedBusinessObjectiveIDs.removeValidators([Validators.required]);
    }
  }

  clearForm() {
    this.form.reset();
    this.submitted = false;
  }
}
