import { Component, Input, OnInit } from "@angular/core";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ITrainingPlanModel } from "app/training/models/training-plan/training-plan";
import { TrainingPlanIndexService } from "app/training/services/traning-plan/training-plan-index.service";

@Component({
  selector: "app-general-info-plan-view",
  templateUrl: "./general-info-plan-view.component.html",
  styleUrls: ["./general-info-plan-view.component.scss"],
})
export class GeneralInfoPlanViewComponent extends AppHelpers implements OnInit {
  isUpdate: boolean = false;
  @Input() planId: number = 0;
  plan: ITrainingPlanModel;
  isLoaded: boolean = false;

  constructor(private trainingPlanIndexService: TrainingPlanIndexService) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.planId > 0 && this.checkNoBusinessOrCompany()) this.getPlanById();
  }

  ngOnInit(): void {}

  getPlanById() {
    this.isLoaded = false;
    // this.showLoading();
    this.trainingPlanIndexService
      .getById(this.planId)
      .subscribe((res: IResponse<ITrainingPlanModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.plan = res.data[0];
          this.isLoaded = true;
        }
        else this.errorList(res?.message?.messages);
        //  this.hideLoading();
      });
  }

  returnToView(updatedPlan: ITrainingPlanModel) {
    this.isUpdate = false;
    this.plan = updatedPlan;
  }
}
