import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { trainingRequestToDisplay } from "app/training/mapper/training-request";
import {
  ITrainingRequestIndexModel,
  ITrainingToDisplayModel,
} from "app/training/models/training-request";
import { TrainingRequestService } from "app/training/services/training-request.service";

@Component({
  selector: "app-training-request-index",
  templateUrl: "./training-request-index.component.html",
  styleUrls: ["./training-request-index.component.scss"],
})
export class TrainingRequestIndexComponent
  extends AppHelpers
  implements OnInit {
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ITrainingToDisplayModel>;

  mandatoryColomns: string[] = [
    "Ser",
    "course_name",
    "reason",
    "fromDate",
    "toDate",
    "actions",
  ];
  optionalColomns: string[] = ["note"];

  constructor(public trainingRequestService: TrainingRequestService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
    }
  }

  ngOnInit(): void {
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) {
      this.page.pageSize = pageSize;
    }
    this.removePageParams();
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.trainingRequestService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ITrainingRequestIndexModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(
            trainingRequestToDisplay(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  delete(model: ITrainingToDisplayModel) {
    this.showLoading();
    this.trainingRequestService.Delete(model.id).subscribe((res: IResponse) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          this.getList(this.currentPage, this.page.pageSize);
        }
        this.deleteSuccessfullyMsg(res.message.messageTypeName);
      }
      else {
        this.errorList(res?.message?.messages);
      }
      this.hideLoading();
    });
  }

  onDelete(model: ITrainingToDisplayModel) {
    let msg = this.msgTranslate(model.course_name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(model);
      }
    });
  }
}
