import { Component, Input, OnInit } from "@angular/core";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { implementationToDisplay } from "app/training/mapper/implementation";
import {
  IImplementationDisplayModel,
  IImplementationModel,
} from "app/training/models/implementation/implementation";
import { ImplementationService } from "app/training/services/implementation/implementation.service";

@Component({
  selector: "app-implementation-general-data-view",
  templateUrl: "./implementation-general-data-view.component.html",
  styleUrls: ["./implementation-general-data-view.component.scss"],
})
export class ImplementationGeneralDataViewComponent
  extends AppHelpers
  implements OnInit
{
  isView: boolean = true;
  @Input() implementationId: number = 0;
  implementation: IImplementationDisplayModel;
  isLoaded: boolean = false;

  constructor(private implementationService: ImplementationService) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.implementationId > 0 && this.checkNoBusinessOrCompany())
      this.getImplementationById();
  }

  ngOnInit(): void {}

  getImplementationById() {
    this.isLoaded = false;
    this.implementationService
      .getById(this.implementationId)
      .subscribe((res: IResponse<IImplementationModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.implementation = implementationToDisplay(res.data)[0];
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }

  returnToView(updated: boolean) {
    this.isView = true;
    if (updated) this.getImplementationById();
  }
}
